import { onMounted, ref, watch } from "vue";
export default {
  name: "toast",
  props: {
    tmessage: {
      type: String,
      default: ""
    }
  },
  setup(props, {
    emit
  }) {
    const toastShow = ref(false);
    onMounted(() => {
      watch(() => props.tmessage, newVal => {
        if (newVal != "") {
          toastShow.value = true;
          setTimeout(() => {
            toastShow.value = false;
            setTimeout(() => {
              emit("update:tmessage", "");
            }, 1000);
          }, 2000);
        }
      });
    });
    return {
      toastShow
    };
  }
};